<template>
    <el-container v-if="type=== 'iTalk'">
        <el-aside width="65%">
            <el-input placeholder="标题" v-model="form.zhTitle"/>

            <el-input placeholder="中文字幕" v-model="form.zhVvt"/>

            <el-input placeholder="英文字幕" v-model="form.enVvt"/>

            <el-select value=""
                       v-model="form.label"
                       multiple
                       filterable
                       allow-create
                       default-first-option
                       placeholder="请选择标签">
                <el-option
                        v-for="item in labelList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>
        </el-aside>
        <el-main>
            <el-select placeholder="难度" v-model="form.difficulty" value="" size="mini">
                <el-option label="初级" value="simple"/>
                <el-option label="中级" value="easily"/>
                <el-option label="高级" value="difficulty"/>
                <el-option label="彩蛋题" value="bonus"/>
            </el-select>
        </el-main>
    </el-container>
</template>

<script>
    export default {
        props: {
            type: String,
            form: Object,
        },
        data() {
            return {
                labelList: [],
            }
        },
    }
</script>

<style scoped>
    .el-main {
        padding: 0 20px;
    }

    .el-aside *, .el-main * {
        margin: 15px 0;
    }
</style>