<template>
    <el-container>
        <left-aside/>
        <el-container>
            <!--<el-header></el-header>-->
            <el-main>
                <el-row type="flex" justify="space-between">
                    <el-col :span="12">
                        {{talkInfo.title}}
                    </el-col>
                    <div style="float: right">
                        <div>
                            <el-button size="small" style="margin-right: 15px"
                                       @click="jumpPage('/editMediaInfo/'+currentMediaId)">查看源素材
                            </el-button>

                            <el-dropdown split-button type="primary" size="small" @command="handleCommand"
                                         trigger="click">发布
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item :command="{key:'time',value:''}">定时发布</el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </div>
                    </div>
                </el-row>

                <el-row class="talkInfo">
                    <el-col :span="3">

                        <div>难度</div>

                        <div>英文字幕</div>

                        <div>标签</div>
                    </el-col>
                    <el-col :span="12">
                        <div>
                            <span v-if="talkInfo.difficulty === 'simple'">初级</span>
                            <span v-if="talkInfo.difficulty === 'easily'">中级</span>
                            <span v-if="talkInfo.difficulty === 'difficulty'">高级</span>
                            <span v-if="talkInfo.difficulty === 'bonus'">彩蛋题</span>
                        </div>
                        <div>
                            {{talkInfo.enVvt || '--'}}
                        </div>
                        <div>{{talkInfo.label || '--'}}</div>
                    </el-col>
                </el-row>

                <div v-if="currentType === 'mp4'">
                    <video controls="" preload="none" poster="" width="480" height="400">
                        <source :src="fileInfo.fileUrl" type="video/mp4">
                    </video>
                </div>

                <div v-if="currentType === 'jpg'|| currentType === 'png'">
                    <img :src="fileInfo.fileUrl" width="480" height="400"/>
                </div>

                <div v-if="currentType === 'mp3'">
                    <audio controls="" preload="none" style="width: 480px;height: 400px">
                        <source :src='fileInfo.fileUrl'>
                    </audio>
                </div>

                <release-timing :isShow="isShowSetTime" @hideTiming="isShowSetTime = false"/>

            </el-main>
        </el-container>
    </el-container>
</template>

<script>
    import ReleaseTiming from '../components/ReleaseTimingItem'
    import LeftAside from '../components/LeftAsideItem'

    export default {
        components: {
            ReleaseTiming,
            LeftAside
        },
        data() {
            return {
                isShowSetTime: false,
                currentType:'',
                talkInfo:{},
                currentMediaId:'',
            }
        },
        mounted(){
            this.getOneTalkData();
        },
        methods: {
            jumpPage(key) {
                this.$router.push(key)
            },
            handleCommand(command) {
                if (command.key === 'time') {
                    this.isShowSetTime = true;
                }
            },
            getOneTalkData() {
                this.$axios.get('/lesson/_getone', {params: {lessonId: this.$route.params.id}}).then((res) => {
                    if (!res.data.code) {
                        console.log(res.data.data)
                        this.talkInfo= res.data.data;
                        this.currentMediaId = res.data.data.materialId;
                        this.getOneMediaData(res.data.data.materialId);
                    }
                })
            },
            getOneMediaData(id) {
                this.$axios.get('/material/_getone', {params: {materialId: id}}).then((res) => {
                    if (!res.data.code) {
                        this.getOneFile(res.data.data.fileId);
                    }
                })
            },
            getOneFile(id) {
                this.$axios.get('/file/_getone', {params: {fileId: id}}).then((res) => {
                    if (!res.data.code) {
                        this.currentType = res.data.data.fileName.substring(res.data.data.fileName.length - 3);
                        this.fileInfo = res.data.data;
                    }
                })
            },
        },
    }
</script>

<style scoped>
    .talkInfo {
        font-size: 12px;
        padding: 20px 0;
    }

    .talkInfo div {
        margin: 15px 0;
    }
</style>