<template>
    <el-container>
        <left-aside/>
        <el-container>
            <!--<el-header></el-header>-->
            <el-main>
                <el-row type="flex" justify="space-between">
                    <el-col :span="5">
                        <div style="font-size: 20px;height: 37px">
                            iWords
                        </div>
                    </el-col>
                </el-row>

                <filter-list listType="word" :filterList="filterList"/>

                <el-button type="primary" @click="jumpPage('/editIWordInfo/new')" size="small">添加</el-button>

                <el-table
                        ref="multipleTable"
                        :data="wordsList"
                        tooltip-effect="dark"
                        style="width: 100%"
                        @selection-change="">
                    <el-table-column
                            type="selection"
                            width="55">
                    </el-table-column>
                    <el-table-column
                            label="标题">
                        <template slot-scope="scope">{{ scope.row.title }}</template>
                    </el-table-column>
                    <el-table-column
                            prop="_id"
                            label="ID">
                    </el-table-column>
                    <el-table-column
                            prop="difficulty"
                            label="难度">
                        <template slot-scope="scope">
                            <span v-if="scope.row.difficulty === 'simple'">初级</span>
                            <span v-if="scope.row.difficulty === 'easily'">中级</span>
                            <span v-if="scope.row.difficulty === 'difficulty'">高级</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                            prop="status"
                            label="状态">
                        <template slot-scope="scope">
                            <span v-if="scope.row.status === 0">未发布</span>
                            <span v-if="scope.row.status===  1">待发布</span>
                            <span v-if="scope.row.status===  2">已发布</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                            label="操作">
                        <template slot-scope="scope">
                            <el-button type="text" size="small" @click="jumpPage('/editIWordInfo/'+scope.row._id)">编辑
                            </el-button>
                            <el-dropdown class="dropdown-edit" trigger="click" @command="handleCommand">
                              <span class="el-dropdown-link">
                                发布<i class="el-icon-arrow-down el-icon--right"></i>
                              </span>
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item :command="{key:'release',value:scope.row}">发布</el-dropdown-item>
                                    <el-dropdown-item :command="{key:'time',value:scope.row}">定时发布</el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </template>
                    </el-table-column>
                </el-table>
            </el-main>
            <el-footer style="text-align: center">
                <el-pagination
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage"
                        :page-size="paginationSize"
                        layout="total, prev, pager, next"
                        :total="paginationNum">
                </el-pagination>
            </el-footer>

            <release-timing :isShow="isShowSetTime" :id="currentId" @hideTiming="isShowSetTime = false"/>
        </el-container>
    </el-container>
</template>

<script>
    import FilterList from '../components/FilterListItem'
    import ReleaseTiming from '../components/ReleaseTimingItem'
    import LeftAside from '../components/LeftAsideItem'

    export default {
        components: {
            FilterList,
            ReleaseTiming,
            LeftAside
        },
        data() {
            return {
                wordsList: [],
                multipleSelection: [],
                currentPage: 1,
                isShowSetTime: false,
                paginationNum: 0,
                paginationSize: 10,
                filterList: {},
                currentId: '',
            }
        },
        mounted() {
            this.init();
        },
        methods: {
            init() {
                this.getWordsListData();
                this.getWordsNumData();
            },
            getWordsListData() {
                this.$axios.get('/lesson/_find', {
                    params: {
                        page: this.currentPage,
                        num: this.paginationSize,
                        category: 'iWord'
                    }
                }).then((res) => {
                    console.log(res)
                    if (!res.data.code) {
                        this.wordsList = res.data.data
                    }
                })
            },
            getWordsNumData() {
                this.$axios.get('/lesson/_getnum', {
                    params: {
                        page: this.currentPage,
                        category: 'iWord'
                    }
                }).then((res) => {
                    this.paginationNum = res.data.data;
                })
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                this.getWordsListData();
            },
            jumpPage(key) {
                this.$router.push(key)
            },
            handleCommand(command) {
                if (command.key === 'time') {
                    this.isShowSetTime = true;
                    this.currentId = command.value._id;
                } else {
                    this.postUpdateWordData(command.value);
                }
            },
            postUpdateWordData(command) {
                let data = new FormData();
                data.append('lessonId', command._id);
                data.append('status', 2);

                this.$axios.post('/lesson/_update', data).then((res) => {
                    console.log(res);
                    if (!res.data.code) {
                        this.getWordsListData();
                    }
                })
            },
        },

    }
</script>

<style scoped>
    .el-dropdown-link {
        cursor: pointer;
        color: #409eff;
        font-size: 12px
    }

    .dropdown-edit {
        margin-left: 5px
    }
</style>