<template>
    <el-container>
        <media-aside :file-info="fileInfo"/>
        <el-container>
            <!--<el-header></el-header>-->

            <el-main>
                <el-row type="flex" justify="space-between">
                    <el-col :span="3">
                        <div>基本信息</div>
                    </el-col>
                    <el-col :span="5">
                        <div>
                            <el-button size="small" @click="postCreateCMSData">生成CMS文章</el-button>

                            <el-button type="primary" size="small" @click="postMediaInfoData">保存</el-button>
                        </div>
                    </el-col>
                </el-row>



                <div style="margin: 40px 0">
                    <span class="typeTitle">课程</span>
                    <el-select placeholder="请选择" v-model="currentType" value="" size="mini">
                        <el-option label="iFun" value="iFun"/>
                        <el-option label="iTalk" value="iTalk"/>
                        <el-option label="iListen" value="iListen" disabled/>
                        <el-option label="iWords" value="iWord" disabled/>
                    </el-select>
                </div>

                <talk-info :type="currentType" :form="iTalkForm"/>

                <fun-info :type="currentType" :form="iFunForm"/>
            </el-main>


            <el-footer></el-footer>
        </el-container>
    </el-container>
</template>

<script>
    import TalkInfo from '../components/TalkInfoItem'
    import FunInfo from '../components/FunInfoItem'
    import MediaAside from '../components/MediaAsideItem'

    export default {
        name: "edit-media-info",
        components: {
            TalkInfo,
            FunInfo,
            MediaAside
        },
        data() {
            return {
                currentType: 'iFun',
                iTalkForm: {
                    // zhTitle:'',
                    // zhSubtitle:'',
                    // enSubtitle:'',
                    // currentLabel:'',
                    // currentDifficulty:''
                },
                iFunForm: {
                    // zhTitle:'',
                    // enTitle:'',
                    // abstract:'',
                    // currentLabel:'',
                    // zhSubtitle:'',
                    // enSubtitle:'',
                    // currentType:'',
                    // currentDifficulty:''
                },
                fileInfo:{}
            }
        },
        mounted() {
            this.getOneMediaData();
        },
        methods: {
            postCreateCMSData(form) {
                form = this.saveMediaInfo();

                let data = new FormData();
                delete form.label;
                form.materialId = this.$route.params.id;
                form.source = 'upload';
                form.status = 0;
                form.category = this.currentType;

                Object.keys(form).forEach(value => {
                    data.append(value, form[value])
                });
                this.$axios.post('/material/_createCMS', data).then((res) => {
                    console.log(res);
                    if (!res.data.code) {
                        this.$message.success('创建成功');
                        this.postMediaInfoData();
                    }
                });
            },
            getOneMediaData() {
                this.$axios.get('/material/_getone', {params: {materialId: this.$route.params.id}}).then((res) => {
                    console.log(res.data.data)
                    if (!res.data.code) {
                        this.getMediaFormInfo(res);
                        this.getOneFile(res.data.data.fileId)
                    }
                })
            },
            getOneFile(id) {
                this.$axios.get('/file/_getone', {params: {fileId: id}}).then((res) => {
                    if (!res.data.code) {
                        this.fileInfo = res.data.data;
                    }
                })
            },
            getMediaFormInfo(res) {
                if (res.data.data.category) {
                    this.currentType = res.data.data.category;
                    res.data.data.label = res.data.data.label || [];
                    switch (res.data.data.category) {
                        case 'iFun':
                            this.iFunForm = res.data.data;
                            break;
                        case 'iTalk':
                            this.iTalkForm = res.data.data;
                            break;
                    }
                }
            },
            saveMediaInfo() {
                let form = {};
                switch (this.currentType) {
                    case 'iFun':
                        if (this.checkIFunForm()) {
                            form = this.iFunForm
                        } else {
                            this.$message.warning('请检查字段是否填写完整！')
                        }
                        break;
                    case 'iTalk':
                        if (this.checkITalkForm()) {
                            form = this.iTalkForm
                        } else {
                            this.$message.warning('请检查字段是否填写完整！')
                        }
                        break;
                }
                return form;
            },
            checkITalkForm() {
                return this.iTalkForm.zhTitle && this.iTalkForm.zhVvt && this.iTalkForm.enVvt && this.iTalkForm.difficulty
            },
            checkIFunForm() {
                return this.iFunForm.zhTitle && this.iFunForm.enTitle && this.iFunForm.abstract &&
                    this.iFunForm.zhVvt && this.iFunForm.enVvt && this.iFunForm.class && this.iFunForm.difficulty
            },
            postMediaInfoData(data) {
                data = this.saveMediaInfo();
                let formData = new FormData();
                delete data.label;
                data.materialId = this.$route.params.id;
                data.source = 'upload';
                data.category = this.currentType;

                Object.keys(data).forEach(value => {
                    formData.append(value, data[value])
                });

                this.$axios.post('/material/_update', formData).then((res) => {
                    console.log(res);
                    if (!res.data.code) {
                        this.$message.success('更新成功');
                    }
                })
            },
        },
        watch:{
            iFunForm(newFun,oldFun){
                console.log(newFun)
            },
        }
    }
</script>

<style scoped>
    .sourceInfo {
        color: rgba(136, 136, 136, 1);
        font-size: 14px;
        text-align: left;
        margin: 20px 0;
    }

    .typeTitle {
        margin-right: 15px;
        font-size: 14px;
    }
</style>