<template>
    <el-aside width="200px">
        <div>
            <div @click="jumpPage('/mediaList')" class="backBlock">
                <i class="el-icon-back"></i>
                <span class="backText">返回</span>
            </div>

            <div v-if="currentType === 'mp4'">
                <video controls="" preload="none" poster="" width="200">
                    <source :src="fileInfo.fileUrl" type="video/mp4">
                </video>
            </div>

            <div v-if="currentType === 'jpg'|| currentType === 'png'">
                <img :src="fileInfo.fileUrl" width="200" height="100"/>
            </div>

            <div v-if="currentType === 'mp3'">
                <audio controls="" preload="none" style="width: 200px;height: 100px">
                    <source :src='fileInfo.fileUrl'>
                </audio>
            </div>

            <div class="backBlock">
                <div class="backBlock" @click="jumpPage('/editMediaInfo/'+$route.params.id)">基本信息</div>
                <div class="editBlock" @click="jumpPage('/editMaterialInfo/'+$route.params.id)">编辑素材</div>
            </div>
        </div>
    </el-aside>
</template>

<script>
    export default {
        props: {
            fileInfo: Object,
        },
        data() {
            return {
                currentType: '',
            }
        },
        mounted() {
            console.log(this.fileInfo)
        },
        methods: {
            jumpPage(key) {
                console.log(key)
                this.$router.push(key)
            },
        },
        watch: {
            fileInfo(newFile, oldFile) {
                console.log(newFile)
                this.currentType = newFile.fileName.substring(newFile.fileName.length - 3);
            },
        }
    }
</script>

<style scoped>
    .backBlock {
        padding: 20px;
        font-size: 14px;
    }

    .backText {
        margin-left: 10px;
    }

    .editBlock {
        padding: 0 20px;
    }

    aside {
        width: 200px;
        height: -webkit-fill-available;
        border-right: 1px solid #e6e6e6;;
    }
</style>