<template>
    <div>
        <el-table
                ref="multipleTable"
                :data="mediaList"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectChange">
            <el-table-column
                    type="selection"
                    width="55">
            </el-table-column>
            <el-table-column
                    prop="zhTitle"
                    label="标题">
                <template slot-scope="scope">{{scope.row.zhTitle}}</template>
            </el-table-column>
            <el-table-column
                    prop="category"
                    label="类别">
            </el-table-column>
            <el-table-column
                    prop="difficulty"
                    label="难度">
                <template slot-scope="scope">
                    <span v-if="scope.row.difficulty === 'simple'">初级</span>
                    <span v-if="scope.row.difficulty === 'easily'">中级</span>
                    <span v-if="scope.row.difficulty === 'difficulty'">高级</span>
                </template>
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="状态"
                    show-overflow-tooltip>
            </el-table-column>

            <el-table-column
                    label="操作" v-if="tableType === 'media'">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="jumpPage('/editMediaInfo/'+scope.row._id)">编辑
                    </el-button>
                    <el-button type="text" size="small" style="color: #F56C6C;" @click="delMediaInfo(scope.row._id)">
                        刪除
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        props: {
            tableType: String,
            mediaList: Array,
            newRowList: Array,
        },
        data() {
            return {
                test: {
                    "_id": "5c2212f49a87ab4ea5b8a3d4",
                    "fileId": "5c2212f49a87ab4ea5b8a3d3",
                    "zhTitle": "12",
                    "__v": 0,
                    "category": "iTalk",
                    "difficulty": "simple",
                    "enVvt": "1",
                    "source": "upload",
                    "zhVvt": "1"
                }
            }
        },
        mounted() {
        },
        methods: {
            jumpPage(key) {
                this.$router.push(key)
            },
            toggleSelection(rows) {
                if (rows) {
                    rows.forEach(row => {
                        this.$refs.multipleTable.toggleRowSelection(row);
                    });
                } else {
                    this.$refs.multipleTable.clearSelection();
                }
            },
            testToggleSelection(rows) {
                console.log(rows);
                let arr =[];
                this.mediaList.forEach((value)=>{
                    if(rows.indexOf(value.fileId)!==-1){
                        console.log(value);
                        arr.push(value)
                    }
                });

                this.toggleSelection(arr)
            },
            delMediaInfo(id) {
                this.$emit('delMediaInfo', id)
            },
            clearSelect(){
                this.$refs.multipleTable.clearSelection();
            },
            handleSelectChange(val) {
                this.$emit('select', val)
            },
        },
        watch: {
        }
    }
</script>

